import Observer from './observer';
import { modelForThreeByThree } from '../utils/const';
import { shuffleGame } from '../utils/utils-for-model';
import Stack from '../utils/stack';


export default class GameModel extends Observer {
  constructor() {
    super();
    this._correctPositionGame = [];
    this._currentGame = [];
    this._logGame = {};
  }

  init() {
    this._correctPositionGame = modelForThreeByThree;

    this._currentGame = shuffleGame(modelForThreeByThree.slice(), this._logGame)
  }
}
